.article-title {
  font-size: 30px;
  font-weight: bold;

  a {
    //color: #ff53a5;
  }
}

.post-meta {
  font-weight: 300;
  margin-bottom: 30px;
}

.article-content,
.home .post,
.tag .post,
.page,
.comment-body {
  border-bottom: 1px solid rgba(0, 0, 0, 0.11);
  margin-bottom: 20px;
}

.separator {
  margin: 0 3px;
}

article p,
.intro p {
  font-family: 'Lora', serif;
  font-size: 18px;
  font-weight: normal;
  line-height: 1.68em;
  margin-bottom: 20px;

  a {
    color: #ff53a5;
  }
}

.view-article {
  display: block;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  margin-top: 20px;
  color: rgba(0,0,0,.44);
  font-size: 16px;
}

.tag-title {
  background: #ff53a5;
  color: #fff;
  padding: 8px;
  display: inline-block;
  margin-bottom: 10px;
}
